'use client'

import { Input } from "@/components/Input";
import { Logo } from "@/components/Logo";
import { useAuth } from "@/hooks/useAuth";
import { yupResolver } from "@hookform/resolvers/yup";
import { useMutation } from "@tanstack/react-query";
import Link from "next/link";
import { redirect } from "next/navigation";
import { Controller, useForm } from "react-hook-form";
import * as yup from "yup";

interface SignInFormData {
    email: string;
    password: string;
}

const signInSchema = yup.object({
    email: yup.string().email("Email inválido").required("O email é obrigatório"),
    password: yup.string().required("A senha é obrigatória"),
})

export default function SignIn() {
    const { control, handleSubmit, formState: { errors } } = useForm<SignInFormData>({
        resolver: yupResolver(signInSchema),
    });

    const { signIn } = useAuth();

    const {mutateAsync: signInFn} = useMutation({
        mutationKey: ['signIn'],
        mutationFn: async (data: SignInFormData) => {
            await signIn(data.email, data.password);
        },
        onSuccess: () => {
            redirect('/List');
        },
        onError: (error) => {
            console.error("Erro ao fazer login:", error);
        }
    })

    async function onSubmit(data: SignInFormData) {
        await signInFn(data);
    }

    return (
        <div className="w-screen h-screen flex flex-row flex-wrap-reverse lg:grid lg:grid-cols-2">
            <div className="flex w-full items-center justify-center">
                <section className="min-w-xl bg-slate-700/40  p-6 rounded-2xl shadow-2xl">
                    <h1 className="text-4xl font-bold text-transparent bg-linear-to-r from-initial via-mid to-final bg-clip-text">
                        Faça Login agora!
                    </h1>
                    <h3 className="text-slate-300">É necessário estar autenticado para visualizar a lista de usuários!</h3>
                    <form className="mt-10 flex flex-col gap-4">
                        <Controller
                            control={control}
                            name="email"
                            render={({ field: { onChange, value } }) => (
                                <Input
                                    label="Email"
                                    onChange={onChange}
                                    value={value}
                                    errors={errors.email?.message}
                                />
                            )}
                        />
                        <Controller
                            control={control}
                            name="password"
                            render={({ field: { onChange, value } }) => (
                                <Input
                                    label="Password"
                                    type="password"
                                    onChange={onChange}
                                    value={value}
                                    errors={errors.password?.message}
                                />
                            )}
                        />
                        <button 
                            onClick={handleSubmit(onSubmit)} 
                            className="mt-6 cursor-pointer w-full rounded-lg bg-linear-to-r from-initial via-mid to-final p-2 font-bold text-sm"
                        >
                            Fazer Login
                        </button>
                        <Link 
                            href="/SignUp"
                            className="text-slate-300 text-sm font-bold text-center mt-4 hover:text-slate-500 transition-colors duration-200"
                        >
                            Precisa apenas cadastrar, clique aqui!
                        </Link>
                    </form>
                </section>
            </div>
            <div className="flex w-full flex-col items-center justify-center gap-4">
                <Logo />
                <h3 className="uppercase text-sm text-slate-500 font-bold">Este é um Sistema de CRUD de Usuários.</h3>
            </div>
        </div>
    )
}
